$font_path: '../../fonts/';

@import '../../scss/helpers/helpers-all';
@import '../../scss/reset';
// ---------------------------------------------------------------------------------fonts#
@include font_face('SFProText-Bold', 'SFProText', $font_path, 700);
@include font_face('SFProText-Heavy', 'SFProText', $font_path, 800);
@include font_face('SFProText-Light', 'SFProText', $font_path, 300);
@include font_face('SFProText-Regular', 'SFProText', $font_path, 400);
// ---------------------------------------------------------------------------------fonts##

// // ---------------------------------------------------------------------------------base#
$point_1: 1200px;
$point_2: 1024px;
$point_3: 1023px;
$point_4: 768px;
$point_5: 640px;
$point_6: 428px;
$point_7: 375px;
$point_8: 320px;

:root {
	--full-screen: calc(var(--vh, 1vh) * 100);

	--content-width: 120rem;

	--font_1: 'SFProText', sans-serif;
	// --font-size-html: calc(100vw / 1920 * 10);
	--font-size-body: 1em;
	--color-text-base: #000;
	--gl-indent: 1.5em;

	--black_1: #000;
	--black_2: #333333;
	--white_1: #fff;
	--purple_1: #422178;
	--purple_2: #370e79;
	--red: #890303;
	--gray: #d9d9d9;

	--body-background: var(--white_1);
	--accent-color: #4aa3df;
	--input-placeholder-color: var(--color-text-base);
}

html {
	height: 100%;

	font-family: var(--font_1);
	font-weight: 400;
	line-height: 1.33;

	@include bp($point_6, min) {
		font-size: 10px;
	}

	@include bp($point_6 - 1) {
		font-size: calc(100vw / 428 * 10);
	}
}

body {
	position: relative;

	height: 100%;

	font-size: var(--font-size-body);
	color: var(--color-text-base);

	-webkit-text-size-adjust: none;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;

	background: var(--body-background);

	transition: all 0.3s ease-in-out;
}

.wrapper {
	position: relative;

	width: 100%;
	display: flex;
	flex-direction: column;
	min-height: 100%;
	height: auto !important;

	overflow-x: hidden;
}

.base {
	width: 100%;
	position: relative;

	flex: 1 0 auto;
}

.section {
	position: relative;

	width: 100%;

	font-size: 1rem;

	//remove this demo style
	&.demo_mod {
		@include flex_block(row, nowrap, center, center, center);

		height: var(--full-screen);

		font-size: 4rem;
		color: var(--white_1);
		text-align: center;

		background-color: var(--black_1);
	}
}

.section_in {
	max-width: var(--content-width);
	width: 100%;
	margin: 0 auto;
}

a {
	text-decoration: none;
	color: var(--accent-color);

	&:hover,
	&:focus {
		text-decoration: underline;
	}
}
// ---------------------------------------------------------------------------------base##
// ---------------------------------------------------------------------------------styles for components#
.product_carousel {
	max-width: var(--content-width);
	margin: 0 auto;

	// background-color: var(--white_1);
	background-color: transparent;
}

.product_carousel__slider {
	position: relative;

	@include bp(429px, min) {
		max-width: 42.8em;
	}
}

.product_carousel__arrow {
	position: absolute;
	z-index: 10;

	top: 4.8em;
	left: 2.4em;

	width: 4.8em;
	height: 4.8em;

	padding: 1em;
	color: var(--black_2);

	transition: opacity 0.3s ease;

	cursor: pointer;

	transition: color 0.3s ease 0s;

	&:hover,
	&:focus {
		color: var(--red);
	}

	&.swiper-button-disabled {
		opacity: 0;
		pointer-events: none;
	}
}

.product_carousel__heart {
	position: absolute;
	z-index: 10;

	top: 4.8em;
	right: 2.4em;

	width: 3.6em;
	height: 3.6em;
	padding: 0.6em;

	color: var(--black_2);

	cursor: pointer;
	background-clip: content-box;

	&:hover,
	&:focus {
		color: var(--red);
	}
}

.product_carousel__arrow .icon,
.product_carousel__heart .icon {
	display: block;

	fill: currentColor;

	width: 100%;
	height: 100%;
}

.product_carousel__pagination {
	position: absolute;
	z-index: 10;

	bottom: 61.9em;
	left: 4.8em;

	width: auto;
	height: 0.8em;

	display: flex;
	align-items: center;
	flex-wrap: wrap;

	.swiper-pagination-bullet {
		display: block;

		width: 0.8em;
		height: 100%;

		background-color: var(--gray);
		border-radius: 5em;
		opacity: 100%;
		transition: all 0.3s ease-in-out;

		&:not(:last-child) {
			margin-right: 1.2em;
		}
	}

	.swiper-pagination-bullet-active {
		width: 1.6em;
		background-color: #422078;
		border-radius: 1.9em;
	}

	.swiper-pagination-bullet-active-next {
		&::after {
			transform: translateX(2rem);
			opacity: 0;
		}
	}
}

.product_carousel__list {
	min-height: 100%;
}

.product_carousel__item {
	position: relative;

	display: flex;
	flex-direction: column;

	min-height: var(--full-screen);

	opacity: 1;
	transition: opacity 0.3s ease-in-out;

	&.inactive {
		opacity: 0;
	}

	@include bp(428px, min) {
		padding: 6.6em var(--gl-indent) 2.5em;
	}

	@include bp(428px - 1) {
		padding: 1.6em var(--gl-indent);
	}
}

.product_carousel__item_decor {
	position: absolute;
	z-index: 1;

	right: -23.4em;
	bottom: 28em;

	// width: 92%;
	// height: 64.2%;

	width: 36.3em;
	height: 36.7em;

	border-radius: 1.2em;
	transform: rotate(30deg);

	.product_carousel__item:nth-child(4n + 1) & {
		background: linear-gradient(180deg, #4232a4 0%, #7852c9 100%);
	}

	.product_carousel__item:nth-child(4n + 2) & {
		background: linear-gradient(180deg, #b19e6a 0%, #c97d52 100%);
	}

	.product_carousel__item:nth-child(4n + 3) & {
		background: linear-gradient(180deg, #b16a95 0%, #c95267 100%);
	}

	.product_carousel__item:nth-child(4n + 4) & {
		background: linear-gradient(180deg, #494e5f 0%, #222324 100%);
	}
}

.product_carousel__item_content {
	position: relative;
	z-index: 2;

	width: 100%;
	flex-shrink: 0;

	margin-top: auto;
	padding-left: 3.3em;

	&:not(:last-child) {
		margin-bottom: 2.4em;
	}
}

.product_carousel__item_title {
	max-width: 5.4em;
	font-weight: 400;
	font-size: 4.8em;
	line-height: 1.2;

	color: var(--black_1);

	overflow: hidden;
	text-overflow: ellipsis;
	display: -moz-box;
	-moz-box-orient: vertical;
	display: -webkit-box;
	-webkit-line-clamp: 2;
	-webkit-box-orient: vertical;
	line-clamp: 2;
	box-orient: vertical;

	&:not(:last-child) {
		margin-bottom: 0.55em;
	}
}

.product_carousel__item_wrap {
	position: relative;
	// transform: translateX(-4.8em);

	// width: 42.8em;
	// height: 43.1em;

	width: 100%;
	padding-bottom: 100%;

	&:not(:last-child) {
		margin-bottom: 1em;
	}
}

.product_carousel__item_pic {
	position: absolute;
	z-index: 100;
	// width: 100%;
	// height: 100%;
	width: 117%;
	height: 81%;
	left: -4.7em;
	top: 0;

	transform: rotate(-45deg);
}

.product_carousel__item_img {
	display: block;
	width: 100%;
	height: 100%;

	object-fit: cover;
}

.product_carousel__item_price {
	font-weight: 800;
	font-size: 4.8em;
	line-height: 1.2;

	color: var(--black_1);

	&:not(:last-child) {
		margin-bottom: 0.24em;
	}
}

.product_carousel__item_description {
	min-height: 2.4em;

	font-weight: 300;
	font-size: 2.4em;
	line-height: 1.2;

	color: var(--black_1);

	overflow: hidden;
	text-overflow: ellipsis;
	display: -moz-box;
	-moz-box-orient: vertical;
	display: -webkit-box;
	-webkit-line-clamp: 2;
	-webkit-box-orient: vertical;
	line-clamp: 2;
	box-orient: vertical;

	&:not(:last-child) {
		margin-bottom: 1.4em;
	}
}

.product_carousel__item_btn {
	display: inline-flex;
	justify-content: center;
	align-items: center;

	height: 2.5em;
	width: 100%;

	padding: 0.6em;

	text-align: center;
	white-space: nowrap;

	background: var(--purple_1);
	border-radius: 0.2em;

	font-weight: 700;
	font-size: 2.4em;
	line-height: 4;
	letter-spacing: 0.02em;

	color: var(--white_1) fff;

	transition: background 0.3s ease;

	cursor: pointer;

	&:hover,
	&:focus {
		text-decoration: none;
		background-color: var(--purple_2);
	}
}

// ---------------------------------------------------------------------------------styles for components##
